import React, { useMemo, useRef, useState } from 'react';
import {
  DataSheetGrid,
  DynamicDataSheetGrid, intColumn, keyColumn, textColumn
} from 'react-datasheet-grid';
import 'react-datasheet-grid/dist/style.css';
import OptionContent from '../Components/OptionContent/OptionContent';
import StringUtil from '../Utils/StringUtil';
import style from './ReadOptions.module.css';

interface Column {
  name: string,
  size: number | null,
}

type DataRecode = { [key: string]: string }

function ReadOptions() {
  // const mobileQuery = useMobileQuery()
  // const { previewMode, setPreviewMode } = useContext(TextStateContext);
  const fileElemRef = useRef<HTMLInputElement>(null)

  const [defs, setDefs] = useState<Column[]>([
    { name: "", size: 4 },
    { name: "", size: 2 },
    { name: "", size: 14 },
    { name: "", size: 1 },
    { name: "", size: 3 },
    { name: "", size: 20 },
    { name: "", size: 2 },
  ])

  const dataColumns = useMemo(() => defs.filter(dev => dev.name).map((def, index) => {
    console.log(defs)
    return { ...keyColumn(`key${index}`, textColumn), title: def.name }
  }), [defs])

  const [activePage, setActivePage] = useState(0)
  const [file, setFile] = useState<File | null>(null)
  const [data, setData] = useState<DataRecode[]>([])

  /**
   * 
   */
  const handleExecute = () => {
    if (file) {
      const reader = new FileReader()
      reader.readAsText(file, 'Shift-JIS')
  
      //
      reader.onload = () => {
        splitText(reader.result as string)
        setActivePage(1)
      };
    }
  }

  /**
   * 
   * @param text 
   */
  const splitText = (text: string) => {
    const lines = text.split(/\n/)
    setData(lines.map(splitLine))
  }

  /**
   * 
   * @param line 
   * @returns 
   */
  const splitLine = (line: string): DataRecode => {
    let offset = 0
    let index = 0
    let obj: DataRecode = {}
    for (const def of defs) {
      const value = StringUtil.substrByte(line, offset, def.size ?? 0)
      obj[`key${index}`] = value
      offset += def.size ?? 0
      index++
    }
    return obj
  }

  return (
    <div>
      <ul className={style.tabs}>
        <li
          className={activePage == 0 ? style.active : ''}
          onClick={() => setActivePage(0)}
        ></li>
        <li
          className={activePage == 1 ? style.active : ''}
          onClick={() => setActivePage(1)}></li>
      </ul>
      <div className={style.contentWrapper}>
        {activePage == 0 && (
          <div className={style.area}>
            <OptionContent
              defs={defs}
              onChangeDefs={setDefs}
              onChangeFile={setFile}
              onExecute={handleExecute}
            />
          </div>
        )}
        {activePage == 1 && (
          <div className={style.area}>
            <div>
              <DynamicDataSheetGrid
                value={data}
                columns={dataColumns}
                onChange={(newData) => {
                  console.log(newData)
                  setData(newData as DataRecode[])
                }}
                addRowsComponent={false}
              />
            </div>
          </div>
        )}
      </div>
    </div>
  );
}

export default ReadOptions;
