import React, { useRef } from 'react';
import {
  DataSheetGrid, intColumn, keyColumn, textColumn
} from 'react-datasheet-grid';
import style from './OptionContent.module.css';

interface Column {
  name: string,
  size: number | null,
}

interface OptionContentProp {
  defs: Column[],
  onChangeDefs: (value: Column[]) => void,
  onChangeFile: (file: File | null) => void,
  onExecute: () => void,
}

function OptionContent({
  defs,
  onChangeDefs,
  onChangeFile,
  onExecute,
}: OptionContentProp) {

  //
  const columns = [
    { ...keyColumn('name', textColumn), title: '' },
    { ...keyColumn('size', intColumn), title: '', width: '0 0 100px' },
  ]

  //
  const fileElemRef = useRef<HTMLInputElement>(null)

  /**
   * 
   * @param e 
   */
  const onFileInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const elem = fileElemRef.current
    if (elem && elem.files && elem.files.length > 0) {
      const file = elem.files[0]
      onChangeFile(file)
    } else {
      onChangeFile(null)
    }
  }

  return (
    <div className={style.contentWrapper}>
      <div className={style.defWrapper}>
        <p>STEP1</p>
        <DataSheetGrid
          value={defs}
          autoAddRow={true}
          onChange={(newDefs) => onChangeDefs(newDefs as Column[])}
          columns={columns}
          createRow={() => ({ name: "", size: 0 })}
          height={400}
        />
      </div>
      <div className={style.fileWrapper}>
        <p>STEP2</p>
        <input
          ref={fileElemRef}
          type="file"
          onChange={onFileInputChange} />

        <div className={style.buttons}>
          <button onClick={onExecute}></button>
        </div>
      </div>
    </div>
  )
}

export default OptionContent;
